import axios from "axios";
import React from "react";
import { AiFillCheckCircle, AiTwotoneEdit } from "react-icons/ai";
import { useHistory } from "react-router-dom";
import swal from "sweetalert"
import Moment from "moment"
import { BiBed } from "react-icons/bi";
import $ from "jquery"
import StripeCheckout from "react-stripe-checkout";



export default function AdminDashboard() {
  const [type, set_type] = React.useState("");

  const [images, setImage] = React.useState("");
  const [name, set_name] = React.useState("");
  const [person_in_room, set_person] = React.useState("");
  const [price, set_price] = React.useState("");
  const [description, set_description] = React.useState("");
  const [aminities, set_aminities] = React.useState("");
  const [room, set_room] = React.useState("");

  const [data, set_data] = React.useState([]);
  const [rprice,realprice]=React.useState("")

  const history = useHistory();

  const called_api = async (types) => {
    let res;
    if (types == "roomlist") {
      console.log("room list");
      res = await axios.get("/admin/list/room", {
        headers: { Authorization: "Bearer " + localStorage.getItem("admin") },
      });
      console.log(res);
      set_data(res.data.data);
    }
    if (types == "user list") {
      res = await axios.get("/admin/list/user", {
        headers: { Authorization: "Bearer " + localStorage.getItem("admin") },
      });
      console.log(res);
      set_data(res.data.data);

    }else{
      // /admin/bookList
      res = await axios.get("/admin/bookList", {
        headers: { Authorization: "Bearer " + localStorage.getItem("admin") },
      });
      console.log(res);
      set_data(res.data.data);
    }
  };

  React.useState(() => {
    called_api();
  }, [type]);

  $(document).ready(function () {
    var numitems = $("#myList li").length;
    $("ul#myList").css("column-count", Math.round(numitems / 2));
  });

  //     amenities: "wifi, gaming"
  // amount: "5000"
  // description: "ac, pool"
  // id: 1
  // media: "https://heritagehotel.s3.ap-south-1.amazonaws.com/1618290341431flower.jpg"
  // no_of_person: "2"
  // no_of_rooms: 0
  // room_name: "deluxe"
  // status: "active"

  const create = async () => {
      let form = new FormData()
      form.append("room_name",name)
      form.append("description",description)
      form.append("amount",price)
      form.append("amenities",aminities)
      form.append("media",images)
      form.append("no_of_person",person_in_room)
      // form.append("media",images)
      form.append("no_of_rooms",room)
      form.append("discount",rprice)
    //   {
    //     room_name: name,
    //     description: description,
    //     amount: price,
    //     amenities: aminities,
    //     media: images,
    //     no_of_person: person_in_room,
    //     no_of_rooms:room
    //   }
    let res = await axios.post(
      "/admin/create/room",form
      ,
      { headers: { Authorization: "Bearer " + localStorage.getItem("admin") } }
    );
        if(res.status==200){
            swal("done")
        }
  };

  return (
    <>
      <div className="container py-5">
      <button
          className="btn btn-primary mx-5"
          onClick={() => {
            set_type("");
           
          }}
        >
          Booking
        </button>
        <button
          className="btn btn-primary mx-5"
          onClick={() => {
            set_type("roomlist");
            called_api("roomlist");
          }}
        >
          List of Room
        </button>
        <button
          className="btn btn-primary mx-5"
          onClick={() => {
            set_type("create room");
          }}
        >
          Create Room
        </button>
        <button
          className="btn btn-primary mx-5"
          onClick={() => {
            set_type("user list");
            called_api("user list");
          }}
        >
          User List
        </button>
        <button
          className="btn btn-primary mx-5"
          onClick={() => {
            history.push("/edit")
          }}
        >
          Edit Site
        </button>

        {type == "" ? 
        <div style={{marginTop:30}}>
          <span style={{fontSize:12,padding:20}}>Booking List</span>
          <table className="table table-border" style={{fontSize:12,marginTop:20}}>
          <tr className="bg-dark text-white"><td>Customer Name</td> <td>Room</td> <td>Date</td> <td>status</td></tr>

          {data.map((data,i)=>{
            return(
              <tr style={{backgroundColor:data.status=="booked"?"#e8faea":"#f5f5f5"}}>
                <td>{data.name}</td>
                <td>{data.room_name}</td>
                <td>{Moment(data.start_date).format("MMM Do YY")+"--"+Moment(data.end_date).format("MMM Do YY")}</td>
                <td>{data.status}</td>
              </tr>
            )
          })}



          
        </table></div> : type == "roomlist" ? (
          <div style={{marginTop:30}}>
          <span style={{fontSize:12,padding:20}}>Room List</span>
          <table className="table table-border" style={{fontSize:12,marginTop:20}}>
            <tr>
              <td>Name</td>
              <td>price</td>
              <td>Details</td>
              <td>Persons</td>
              <td>Rooms For Booking</td>
              <td>status</td>
              {/* <td>Action</td> */}
            </tr>
            {data.map((data, i) => {
              return (
                <tr>
                  <td>{data.room_name}</td>
                  <td>{data.amount}</td>
                  <td>{data.description}</td>
                  <td>{data.no_of_person}</td>
                  <td>{data.no_of_rooms}</td>
                  <td>
                    {data.status == "active" ? (
                      <span style={{ color: "green" }}>Active</span>
                    ) : (
                      <span style={{ color: "red" }}>InActive</span>
                    )}
                  </td>
                  
                </tr>
              );
            })}
          </table>
          </div>
        ) : type == "create room" ? (
          <div style={{marginTop:30}}>
          <span style={{fontSize:12,padding:20,fontWeight:"bold"}}>Create List</span>
          <div style={{flexDirection:"row"}}>
          <table className="table table-borderless col-8" style={{float:"left"}}>
            <tr>
              <td>Image</td>
              <td>
                <input
                  className="form-control"
                  type="file"
                  onChange={(e) => {
                    setImage(e.target.files[0]);
                  }}
                  single
                />
              </td>
            </tr>
            {/* <tr>
              {images == "" ? null : (
                <td>
                  <img
                    style={{ width: 100, height: 100, marginLeft: "100%" }}
                    src={URL.createObjectURL(images)}
                  />
                </td>
              )}
            </tr> */}
            <tr>
              <td>Name of Room</td>
              <td>
                <input
                  className="form-control"
                  type="text"
                  onChange={(e) => {
                    set_name(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>No. of Room</td>
              <td>
                <input
                className="form-control"
                  type="text"
                  onChange={(e) => {
                    set_room(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>No of person allowed</td>
              <td>
                <input
                className="form-control"
                  type="text"
                  onChange={(e) => {
                    set_person(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>Amount</td>
              <td>
                <input
                  type="text"
                  className="form-control"
                  onChange={(e) => {
                    realprice(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>Discount Amount</td>
              <td>
                <input
                  type="text"
                  className="form-control"
                  onChange={(e) => {
                    set_price(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>Description</td>
              <td>
                <input
                  type="text"
                  className="form-control"
                  onChange={(e) => {
                    set_description(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td>Amenities</td>
              <td>
                <input
                  type="text"
                  className="form-control"
                  onChange={(e) => {
                    set_aminities(e.target.value);
                  }}
                />
              </td>
            </tr>
            <tr>
              <td></td>
              <button
                class="btn btn-warning mt-5"
                onClick={() => {
                  create();
                }}
              >
                Create Room
              </button>
            </tr>
          </table>

                {/* create room car */}
                <div className="col-md-4" style={{float:"left"}}>
                    <div className="card card-cascade card-ecommerce wider shadow mb-5 ">
                      <div className="view view-cascade overlay text-center">
                        <img className="card-img-top" src={images==""?null:window.webkitURL.createObjectURL(images)} alt="" />
                        <a>
                          <div className="mask rgba-white-slight"></div>
                        </a>{" "}
                      </div>
                      <div className="card-body card-body-cascade">
                        <h4 className="card-title">
                          <strong style={{ textAlign: "left" }}>
                            
                            <a>{name}</a>
                          </strong>
                        </h4>
           
                        <div
                          style={{
                            flexDirection: "row",
                            position: "absolute",
                            right: 20,
                          }}
                        >
                          <span>
                            <BiBed size={15} color="grey" />
                            {"     "}
                            {person_in_room}
                          </span>
                          
                        </div>

                        <div className="col">
                          <p className="card-text">{description}</p>
                        </div>
                        <hr />
                        <span>Facilities</span>
                        <div style={{ borderWidth: 1 }}>
                          <ul className="uloo" style={{maxWidth: 250}} >
                            {aminities.split(",").map((data) => {
                              return (
                                <span className="lioo">
                                  {"  "}
                                  {data}
                                </span>
                              );
                            })}
                          </ul>


                          <span style={{color:"red",fontSize:10}}>Room Left {room}</span>
                        </div>

                        <div className="card-footer">
                        <span style={{ color: "red", fontWeight: "bold",textDecoration:"line-through",fontSize:13 }}>
                            {" $ "} {rprice}
                          </span>
                          <span style={{ color: "green", fontWeight: "bold" }}>
                            {" $ "} {price}
                          </span>
                          <div
                            style={{
                              position: "absolute",
                              right: 30,
                              bottom: 20,
                            }}
                          >
                           
                             
                              <StripeCheckout
                                
                              />
                          
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

          </div>
         
          </div>
        
        
        ) : type == "user list" ? (
          <div style={{marginTop:30}}>
          <span style={{fontSize:12,padding:20}}>User List</span>
          <table className="table table-border" style={{fontSize:12,marginTop:20}}>
            <tr>
              <td>Id</td>
              <td>Name</td>
              <td>mobile</td>
              <td>email</td>
            </tr>
            {data.map((data, i) => {
              return (
                <tr>
                  <td>{data.id}</td>
                  <td>{data.name}</td>
                  <td>{data.mobile}</td>
                  <td>{data.email}</td>
                </tr>
              );
            })}
          </table>
          </div>
        ) : null}
      </div>
    </>
  );
}
